h1, p {
  font-family: Lato;
}


.categories{
  display: flex;
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  margin: 20px;
  padding: 0px 19px 10px;
  background: #f5f5dcc2;
  border: 1px solid #00bcd485;
  border-radius: 20px;
}